import React, { useEffect, useState } from 'react';
import { Button, ButtonGroup, Form } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import classNames from 'classnames';
import { OrderStatus, OrderStatusState } from '../../../../types/OrderStatus';
import CheckMark from '../../../../assets/CheckMark.component.svg';
import CloseIcon from '../../../../assets/CloseIcon.component.svg';
import { OrderSort } from '../../../../types/Order';
import { Dispatcher } from '../../../../types/store';
import { editEntity } from '../../../../store/Edit/thunks';

const cx = classNames;

interface IOrderItemStatusButtons {
  currentOrderStatus: OrderStatus,
  filters: OrderSort,
  page: number,
  orderId: string,
}

const OrderItemStatusButtons = ({ currentOrderStatus, filters, page, orderId }: IOrderItemStatusButtons): React.ReactElement => {
  const dispatch = useDispatch<Dispatcher>();
  const [editMode, setEditMode] = useState<boolean>(false);
  const [submitStatusId, setSubmitStatusId] = useState<string | null>(null);
  const { orderStatus, loading } = useSelector<{
    orderStatus: OrderStatusState;
  }, OrderStatusState>((state) => state.orderStatus);

  const onSubmitStatus = (): void => {
    dispatch(editEntity({ }, 'order', orderId, `Заказ №${orderId}, `))
  };
  
  useEffect(() => {
      setSubmitStatusId(orderStatus.find((status) => status.name === 'Подтвержденные'));
  }, [orderStatus])

  return (
    <ButtonGroup className={cx('order-item-status-edit-btn', { 'edit-mode': editMode })}>
      {!editMode && currentOrderStatus.name === 'Новые' && (
        <>
          <Button size="sm" variant="outline-primary">Подтвердить</Button>
          <Button size="sm" variant="outline-danger">Отменить</Button>
        </>
      )}
      {!editMode && currentOrderStatus.name !== 'Новые' && (
        <>
          <Button size="sm" variant="outline-danger">Отменить</Button>
          <Button size="sm" variant="outline-primary" onClick={() => setEditMode(true)}>Изменить</Button>
        </>
      )}
      {editMode && currentOrderStatus.name !== 'Новые' && (
        <>
          <Form.Select
            size="sm"
            disabled={loading}
            className="status-edit-select"
          >
            {orderStatus && orderStatus.map((status) => (
              <option key={status.id} value={status.id}>{status.name}</option>
            ))}
          </Form.Select>
          <Button size="sm" variant="outline-primary" className="submit-btn">
            <CheckMark />
          </Button>
          <Button size="sm" variant="outline-danger" className="cancel-btn">
            <CloseIcon />
          </Button>
        </>
      )}
    </ButtonGroup>
  );
};

export default OrderItemStatusButtons;
